$font-family: Roboto, Arial, Helvetica, sans-serif;
$color-grey: #eee;
$color-pink: #ffbfcb;
$animation-duration: 0.5s;
$padding-box: 10px;
$start-day: 1;
$week-end-day: 7;
$month-length: 31;
$days-of-week: (
  "mon": 0,
  "tue": 1,
  "wed": 2,
  "thu": 3,
  "fri": 4,
  "sat": 5,
  "sun": 6,
);
$day-width: 100px;
$gap: 1px;
